.about {
  flex-direction: column;
  margin-top: 3em;
}

.about__name {
  color: var(--clr-primary);
}

.about> :nth-child(2) {
  margin-top: 1.2em;
  font-size: 2rem;
  line-height: 1.2;
  color: var(--clr-fg-alt);
}

.about__desc {
  font-size: 1rem;
  max-width: 70%;
  text-align: center;
}

.about__desc,
.about__contact {
  margin-top: 2.4em;
}

.about .link--icon {
  margin-right: 0.8em;
}

.about .btn--outline {
  margin-right: 1em;
}

.btnResume {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  border-radius: 5px;
  height: 50px;
  cursor: pointer;
  padding: 0px 25px;
  margin-top: 10px;
}

.btnResume:hover {
  background-color: var(--clr-primary);
  color: var(--clr-bg);
}

.resume-dwnld {
  padding: 10px 0 0 0;

}



@media all and (max-width: 1022px) and (min-width: 800px) {
  .mobileHead {
    align-content: center;
  }

  .app .about {
    align-items: flex-start;
    margin-top: 2em;
    justify-content: center;
    align-items: center;
  }

  .about> :nth-child(2) {
    font-size: 1.5rem;
  }
}

@media (max-width: 375px) {
  .mobileHead {
    align-content: center;
    margin: auto;
    padding: 25px;
  }
}